import React, { useState } from "react";
import { useMutation } from "../../../../Hooks/useMutation";
import { mutationCallback } from "../../../Util/mutationCallback";
import { MessageBoxProps } from "../types/types";
import "./MessageBox.css";

const { ROOT_URL } = require("../../../../constants");

const MessageBox = React.memo(({ sender, activeChatId }: MessageBoxProps) => {
  const [newMessage, setNewMessage] = useState("");

  const { mutate: mutateMessage } = useMutation((data) => {
    const url = `${ROOT_URL}message`;
    return mutationCallback(data, url);
  });

  const { mutate: mutateChat } = useMutation((data) => {
    const url = `${ROOT_URL}chat/id`;
    return mutationCallback(data, url);
  });

  const handleNewMessage = (
    e: React.ChangeEvent<HTMLTextAreaElement>
  ): void => {
    const { value } = e.target;
    setNewMessage(value);
  };

  const handleMessageSend = (): void => {
    const uniqueMessageId = Date.now().toString();
    const messagePayload = {
      id: uniqueMessageId,
      senderId: sender,
      content: newMessage,
      timeStamp: uniqueMessageId,
    };
    mutateMessage(messagePayload);

    const chatPayload = {
      chatId: activeChatId,
      messageId: uniqueMessageId,
    };
    mutateChat(chatPayload);
    setNewMessage("");
  };

  return (
    <div className="messageBox-container">
      <textarea
        className="messageBox-input"
        placeholder="Type Message..."
        value={newMessage}
        onChange={handleNewMessage}
      />
      <button
        disabled={newMessage === ""}
        className="message-box-button"
        onClick={handleMessageSend}
      >
        Send
      </button>
    </div>
  );
});

export { MessageBox };